import React, { useRef } from 'react'
import ReactToPrint from 'react-to-print-advanced'
import ComponentToPrint from './SelfApprovalForm';
import AdvertisementApiList from '../../AdvertisementApiList';
import { useState } from 'react';
import { useEffect } from 'react';
import useSetTitle from '../../../../Components/GlobalData/UseSetTitle';
import axios from 'axios';
import ApiHeader from '../../../../Components/ApiList/ApiHeader';
import { useParams } from 'react-router-dom';
import AxiosInterceptors from '../../../../Components/GlobalData/AxiosInterceptors';


function SelfApprovalIndexForm() {

    ///////{***✅ payment Id from use param...***}///////
    const { id, workflowId } = useParams()
    console.log("param transcation id ..", id)
    console.log("param wrkflow id ..", workflowId)

    const { getApprovalLetterDetails } = AdvertisementApiList();
    const componentRef = useRef();
    const [paymentData, setpaymentData] = useState();
    const [show, setshow] = useState(false)
    // SETTING GLOBAL TITLE AT ONCE USING CUSTOM HOOK
    useSetTitle('Payment Receipt')
    // const [paymentId, setpaymentId] = useState('pay_KiI7acuJomb5eq');

    // const { licenseId, tranId } = useParams();


    useEffect(() => {
        fetchPaymentData();
    }, [])

    const fetchPaymentData = () => {
        const requestBody = {
            applicationId: id,
            workflowId: workflowId,
        }

        console.log('before fetch payment receipt....', requestBody)
        AxiosInterceptors.post(`${getApprovalLetterDetails}`, requestBody, ApiHeader())
            .then((response) => {
                // console.log("payment data", response.data.data);
                console.log("Approval letter details", response);
                if (response.data.status) {
                    setpaymentData(response.data.data);
                } else {
                    ;
                }
            })
            .catch((error) => {

                console.log(error);
            })
    }

    console.log("payment data", paymentData)

    return (
        <>
            <div>

                {/* <NonBlockingLoader show={show} /> */}
                <ReactToPrint
                    trigger={() => <button className="float-right pl-4 pr-6 py-1 mr-4 bg-sky-400 text-white font-medium text-xs leading-tight uppercase rounded   hover: focus: focus:outline-none focus:ring-0  active: transition duration-150 ease-in-out">print</button>}
                    content={() => componentRef.current}
                />
                <ComponentToPrint ref={componentRef} paymentData={paymentData} />
            </div>
        </>
    )
}

export default SelfApprovalIndexForm